import React, { FC } from "react";
import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import TableSortLabel from "@mui/material/TableSortLabel";
interface IProps {}

/**
 * @author
 * @function @
 **/

interface Column {
  id: "name" | "head_count_boy" | "head_count_girl" | "age" | "sido_name" | "status";
  label: string;
  minWidth?: number;
  align?: "right";
  format?: (value: number) => string;
}

const columns: Column[] = [
  { id: "name", label: "", minWidth: 170 },
  {
    id: "head_count_boy",
    label: "",
    minWidth: 50,
    align: "right",
  },
  {
    id: "head_count_girl",
    label: "",
    minWidth: 50,
    align: "right",
  },
  {
    id: "age",
    label: "",
    minWidth: 40,
    align: "right",
  },
  {
    id: "sido_name",
    label: "",
    minWidth: 170,
    align: "right",
  },

  {
    id: "status",
    label: "",
    minWidth: 170,
    align: "right",
  },
];

interface waitingRoom {
  name: string;
  head_count: number;
  head_count_boy: number;
  head_count_girl: number;

  age: String;
  sido_name: String;
  status: number;
}

function createWaitingRoom(
  name: string,
  head_count: number,
  head_count_boy: number,
  head_count_girl: number,
  age: String,
  sido_name: String,

  status: number
): waitingRoom {
  return { name, head_count, head_count_boy, head_count_girl, sido_name, age, status };
}

