const rows = [
  createWaitingRoom("1", 3, 2, 1, "20", "", 0),
  createWaitingRoom("  2", 4, 2, 1, "20", "", 0),
  createWaitingRoom(" 3", 2, 2, 2, "30", "", 1),
  createWaitingRoom("  4", 3, 3, 3, "30", "", 1),
  createWaitingRoom(" 5", 4, 1, 1, "40", "", 0),
  createWaitingRoom("  6", 2, 1, 2, "50 ", "", 0),
  createWaitingRoom(" 7", 4, 2, 2, "20", "", 0),
  createWaitingRoom("  8", 3, 2, 1, "20", "", 0),
  createWaitingRoom(" 9", 3, 3, 3, "30", "", 1),
  createWaitingRoom("  9", 4, 4, 4, "40", "", 1),
  createWaitingRoom(" 10", 2, 2, 2, "50 ", "", 1),
  createWaitingRoom("  11", 3, 2, 1, "20", "", 0),
  createWaitingRoom(" 12", 3, 2, 1, "30", "", 0),
  createWaitingRoom("  13", 3, 2, 1, "40", "", 0),
  createWaitingRoom(" 14", 3, 2, 1, "50 ", "", 0),
  createWaitingRoom("  15", 3, 2, 1, "20", "", 0),
];

export const WaitingRoomList: FC<IProps> = (props) => {
  return (
    <div>
      {" "}
      <Paper
        sx={{ width: "80%", overflow: "hidden", position: "absolute", right: "10%", bottom: "20%" }}
      >
        <TableContainer sx={{ maxHeight: 440 }}>
          <Table stickyHeader aria-label="sticky table">
            <TableHead>
              <TableRow>
                {columns.map((column) => (
                  <TableCell key={column.id} align={column.align}>
                    {column.label}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => {
                return (
                  <TableRow key={row.name}>
                    {columns.map((column) => {
                      const value = row[column.id];
                      return (
                        <TableCell key={column.id} align={column.align}>
                          {column.format && typeof value === "number"
                            ? column.format(value)
                            : value}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </div>
  );
};